.event {
    $event: &;
    position: relative;

    &--detailed {
        max-width: 300px;
        width: 100%;
        background: $white;

        @include bp-md {
            max-width: calc(50% - 40px);
            margin-right: 30px; }
        @include bp-lg {
            max-width: calc(33.33% - 40px); }

        #{$event} {
            &__wrapper {
                height: 380px;
                max-width: 300px;
                @include bp-md {
                    max-width: none; } }

            &__content {
                padding: 40px 30px;
                @include bp-xl {
                    padding: 40px; } }

            &__logo {
                height: 55px; } } }

    // &--upcoming
    //     background-color: $secondary
    //     max-width: 319px
