    //     height: 330px
    //     margin: 0 auto
    //     padding: 50px 0
    //     +bp-md
    //         width: 25%
    //         padding: 0
    //         background-color: transparent
    //         height: auto
    //         margin: 0

    //     #{$event}
    //         &__wrapper
    //             height: 330px
